import type { SelectionBarContextInterface } from './types';

export const MOUNT_SELECTION_TOOLBAR = Symbol('MOUNT_SELECTION_TOOLBAR');

export const UPDATE_LAST_COLOR = Symbol('UPDATE_LAST_COLOR');
export interface UpdateLastColorAction {
  type: typeof UPDATE_LAST_COLOR;
  payload: string;
}

export const UPDATE_LAST_HIGHLIGHT = Symbol('UPDATE_LAST_HIGHLIGHT');
export interface UpdateLastHighlightAction {
  type: typeof UPDATE_LAST_HIGHLIGHT;
  payload: string;
}

export const UPDATE_LAST_FILL = Symbol('UPDATE_LAST_FILL');
export interface UpdateLastFillAction {
  type: typeof UPDATE_LAST_FILL;
  payload: string;
}

export const UPDATE_CAN_HIDE = Symbol('UPDATE_CAN_HIDE');
export interface UpdateCanHideAction {
  type: typeof UPDATE_CAN_HIDE;
  payload: boolean;
}

export type SelectionBarActionType =
  | UpdateLastColorAction
  | UpdateLastHighlightAction
  | UpdateLastFillAction
  | UpdateCanHideAction;

const selectionBarReducer = (
  state: SelectionBarContextInterface,
  action: SelectionBarActionType,
) => {
  if (action.type === UPDATE_LAST_COLOR) {
    return {
      ...state,
      lastColor: action.payload,
    };
  }
  if (action.type === UPDATE_LAST_HIGHLIGHT) {
    return {
      ...state,
      lastHighlight: action.payload,
    };
  }
  if (action.type === UPDATE_LAST_FILL) {
    return {
      ...state,
      lastFill: action.payload,
    };
  }
  if (action.type === UPDATE_CAN_HIDE) {
    return {
      ...state,
      canHide: action.payload,
    }
  }
  return state;
};

export function mountSelectionToolbar() {
  return {
    type: MOUNT_SELECTION_TOOLBAR,
  };
}

export default selectionBarReducer;
